import React, { useState, useEffect } from "react"
import Swal from "sweetalert2"
import Cookie from "js-cookie"

import io from "socket.io-client"

let socket

const Login = ({ history }) => {
    let [username, setUsername] = useState("")
    let [pass, setPass] = useState("")
    let [remember, setRemember] = useState("")

    let ENDPOINT = "https://shorchatserver.herokuapp.com/socket.io/socket.io.js"

    useEffect(() => {
        socket = io(ENDPOINT)

        socket.on("success", async data => {
            if(remember === "on"){
                await Cookie.set("token", data.token, {expires: 30})
                await Cookie.set("name", data.name, {expires: 30})
            } else {
                await Cookie.set("token", data.token, {expires: 1})
                await Cookie.set("name", data.name, {expires: 1})
            }
            history.push("/join")
        })
        socket.on("failed", data => {
            Swal.fire({
                icon: "error",
                title: "Error",
                text: data
            })
        })

        return () => {
            socket.off()
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [ENDPOINT])

    const handleLogin = (e) => {
        let data = {
            name: username,
            pass: pass
        }
        socket.emit("login", data)
    }

    return(
        <div>
            <div className="container mt-6 mb-6">
                <div className="box">
                    <h1 className="title is-1">Login</h1>
                    <div className="field">
                        <label className="label">Username</label>
                        <div className="control has-icons-left">
                            <input onChange={e => setUsername(e.target.value)} placeholder="Username" className="input" />
                            <span className="icon is-small is-left">
                                <i className="fas fa-user"></i>
                            </span>
                        </div>
                    </div>
                    <div className="field">
                        <label className="label">Password</label>
                        <div className="control has-icons-left">
                            <input onChange={e => setPass(e.target.value)} type="password" placeholder="Password" className="input" />
                            <span className="icon is-small is-left">
                                <i className="fas fa-lock"></i>
                            </span>
                        </div>
                    </div>
                    <div className="field">
                        <label className="checkbox">
                            <input onChange={e => setRemember(e.target.value)} type="checkbox" />
                            &nbsp;Remember me
                        </label>
                    </div>
                    <button onClick={handleLogin} className="button is-success">Login</button>
                </div>
            </div>
        </div>
    )
}

export default Login