import React from "react"

import "./InfoBar.css"

const InfoBar = ({ room }) => (
    <nav className="infoBar navbar is-info">
        <div className="navbar-brand">
            <h1 className="title is-1">Chat</h1>
        </div>
        <div className="navbar-start">
            <div className="navbar-item">
                <h3 className="title is-3">{room}</h3>
            </div>
        </div>
        <div className="navbar-end">
        <div className="navbar-item has-dropdown is-hoverable">
            <button className="navbar-link">
                Account
            </button>
            <div className="navbar-dropdown">
                <a href="/edit" className="navbar-item">
                    Edit
                </a>
                <a href="/settings" className="navbar-item">
                    Settings
                </a>
                <hr className="navbar-divider" />
                <a href="/logout" className="navbar-item">
                    Log Out
                </a>
            </div>
        </div>

            <div className="navbar-item">
                <div className="buttons">
                    <div>
                        <a href="/" className="button is-danger">Leave</a>
                    </div>
                </div>
            </div>
        </div>
    </nav>
)

export default InfoBar