import React, { useState, useEffect } from "react"
import { Redirect } from "react-router-dom"
import queryString from "query-string"
import io from "socket.io-client"

import "./Chat.css"

import InfoBar from "../InfoBar/InfoBar"
import Input from "../Input/Input"
import Messages from "../Messages/Messages"
import UsersOnline from "../UsersOnline/UsersOnline"

let socket

const Chat = ({ location }) => {
    const [name, setName] = useState("")
    const [room, setRoom] = useState("")
    const [users, setUsers] = useState("")
    const [message, setMessage] = useState([])
    const [messages, setMessages] = useState([])
    const ENDPOINT = "https://shorchatserver.herokuapp.com/socket.io/socket.io.js"

    useEffect(() => {
        const { name, room } = queryString.parse(location.search)

        socket = io(ENDPOINT)
        
        setName(name)
        setRoom(room)

        socket.emit("join", { name, room }, (error) => {
            if(error){
                alert(error)
                document.location = "/"
            }
        })

        return () => {
            socket.emit("disconnect")

            socket.off()
        }
    }, [ENDPOINT, location.search])

    useEffect(() => {
        socket.on("message", message => {
            setMessages([...messages, message])
        })

        socket.on("roomData", ({users}) => {
            setUsers(users)
        })
    }, [messages])

    /*useEffect(() => {
        socket.on("errorAlert", (text) => {

        })
        socket.on("setLocation", (location) => {
            document.location = location
        })
    }, [])*/

    const sendMessage = (e) => {
        if(!message || message === undefined || message === null) return alert("You cannot send blank messages!")

        e.preventDefault()

        socket.emit("sendMessage", message, () => setMessage(""))
    }

    return (
        <div>
            {(room || name) ? (
            <div className="chatContainer">
                <div>
                    <div className="InfoBar">
                        <InfoBar room={room} />
                    </div>
                    <div className="UsersOnline">
                        <UsersOnline users={users} />
                    </div>
                    <div className="container messagesContainer">
                        <div>
                            <Messages messages={messages} name={name} />
                        </div>
                        <Input message={message} setMessage={setMessage} sendMessage={sendMessage} />
                    </div>
                </div>
            </div>
            ): (
                <Redirect to="/" />
            )}
        </div>
    )
}

export default Chat