import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"

import "./Join.css"

const Join = () => {
    const [name, setName] = useState("")
    const [room, setRoom] = useState("")

    useEffect(() => {
        setRoom("Room 1")
    }, [])

    const roomHandler = (e) => {
        setRoom(e.target.value)
    }

    return (
        <div className="container mt-6 mb-6">
            <div className="box">
                <h1 className="title is-1">Join</h1>
                <div className="field">
                    <label className="label">Username</label>
                    <div className="control has-icons-left">
                        <input placeholder="Name" className="joinInput input" type="text" onChange={(e) => setName(e.target.value)} />
                        <span className="icon is-small is-left">
                            <i className="fas fa-user"></i>
                        </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label">Room</label>
                    <div className="control has-icons-left">
                        {/*<input placeholder="Room" className="joinInput input" type="text" onChange={(e) => setRoom(e.target.value)} />
                        <span className="icon is-small is-left">
                            <i className="fas fa-door-open"></i>
                        </span>*/}

                        <div className="select">
                            <select onChange={roomHandler}>
                                <option value="Room 1">Room 1</option>
                                <option value="Room 2">Room 2</option>
                                <option value="Room 3">Room 3</option>
                                <option value="Room 4">Room 4</option>
                                <option value="Room 5">Room 5</option>
                            </select>
                        </div>
                        <span className="icon is-left">
                            <i className="fa fa-door-open"></i>
                        </span>
                    </div>
                </div>
                <Link onClick={e => (!name || !room) ? e.preventDefault() : null} to={`./chat?name=${name}&room=${room}`}>
                    <button className="button is-success" type="submit">Join</button>
                </Link>
            </div>
        </div>
    )
}

export default Join