import React from "react"
import Cookie from "js-cookie"

const Navbar = ({ history }) => {

    const token = Cookie.get("token")

    const logoutHandler = () => {
        Cookie.remove("token")
        Cookie.remove("name")
        history.push("/")
    }

    return(
        <div>
            <nav className="navbar is-light">
                <div className="navbar-brand">
                    <h1 className="title is-1">Shor Chat</h1>
                </div>      
                <div className="navbar-start">
                    <a href="/" className="navbar-item">
                        Home
                    </a>
                    <a href="/about" className="navbar-item">
                        About
                    </a>
                    <a href="/contact" className="navbar-item">
                        Contact
                    </a>
                </div>
                <div className="navbar-end">
                    {(token === undefined) ? (
                        <div className="navbar-item">
                            <div className="buttons">
                                <a href="/login" className="button is-light">
                                    Log in
                                </a>
                                <a href="/register" className="button is-info">
                                    <strong>Sign up</strong>
                                </a>
                            </div>
                        </div>
                    ): (
                    <div className="navbar-item">
                    <div className="navbar-item has-dropdown is-hoverable">
                        <a href="/account" className="navbar-link">
                            Account
                        </a>
                        <div className="navbar-dropdown">
                            <a href="/account/edit" className="navbar-item">
                                Edit
                            </a>
                            <a href="/account/report" className="navbar-item">
                                Report an issue
                            </a>
                            <a href="/account/support" className="navbar-item">
                                Support
                            </a>
                            <hr className="navbar-divider" />
                            <a href="/" onClick={logoutHandler} className="navbar-item">
                                Log out
                            </a>
                        </div>
                    </div>
                    <div className="buttons">
                        <a href="/join" className="button is-light">
                            Join
                        </a>
                    </div>
                    </div>
                    )}
                </div>
            </nav>
        </div>
    )
}

export default Navbar